import React, {Component} from 'react';
import {Grid, Container, RadioGroup, Radio, Slide, Typography} from "@material-ui/core"
import './App.css';
import piImage from "./images/pi.png"
import tripImage from "./images/tripApp.png"
import hospitalImage from "./images/hospital.png"
import resumeImage from "./images/resume.png"
import ProjectSlide from "./ProjectSlide"

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showTripApp: true,
            showHospitalApp: false,
            showPiProject: false,
            showResume: false
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.renderTripApp = this.renderTripApp.bind(this);
    }
    

    render() {
        return (
        <Container 
            className="projects"
            maxWidth="xl"
            >
            <Typography variant="h4" color="inherit" style={{paddingBottom: 30}}> What I've Built</Typography> 
            <Grid
              item={true}
              container
              spacing={3}
              >
                <Grid item={true} xs={12} className="buttons">
                    {this.renderRadioButtons()}
                </Grid>
                <Grid item={true} xs={12}>
                {this.state.showTripApp && 
                    this.renderTripApp()}
                {this.state.showPiProject && 
                    this.renderPiProject()}
                {this.state.showHospitalApp && 
                    this.renderHospitalApp()}
                {this.state.showResume && 
                    this.renderResume()}
                </Grid>
            </Grid>
        </Container>
        )
    }
    renderRadioButtons() {
        return (
        <div>
            <RadioGroup row defaultValue="tripApp" className="center">
                <Radio
                    value="tripApp"
                    color="primary"
                    name="reactTripApp"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="resume"
                    color="primary"
                    name="resume"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="piProject"
                    color="primary"
                    name="PiProject"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="hospitalApp"
                    color="primary"
                    name="FuelHospital"
                    size="small"
                    onChange={this.handleChange}
                />
        </RadioGroup>
      </div>
        );
    }
    handleChange(event)
    {
        let newState=this.state
        switch(event.target.value) {
            case 'tripApp':
                
                newState.showTripApp = true;
                newState.showPiProject = false;
                newState.showHospitalApp = false;
                newState.showResume = false;
                this.setState({newState});
                break;
            case 'resume':
               
                newState.showTripApp = false;
                newState.showPiProject = false;
                newState.showHospitalApp = false;
                newState.showResume = true;
                this.setState({newState});
                break;
            case 'piProject':
                
                newState.showTripApp = false;
                newState.showPiProject = true;
                newState.showHospitalApp = false;
                newState.showResume = false;
                this.setState({newState});
                break;
            case 'hospitalApp':
                
                newState.showTripApp = false;
                newState.showPiProject = false;
                newState.showHospitalApp = true;
                newState.showResume = false;
                this.setState({newState});
                break;
        }

    }
    renderTripApp()
    {
        return (
            <Slide direction="left" in={this.state.showTripApp}>
                <ProjectSlide
                    picture={tripImage}
                    title="Web Trip Application"
                    description="Created a mobile, single page application and microservices during a semester-long,
                    team-based Agile software development project."
                    languages="Slack, GitHub, ZenHub, TravisCI, Code Climate, JUnit, Jest, Maven, NPM, WebPack,
                    IntelliJ, ReactJS, ReactStrap, Leaflet, JavaScript, HTTP, JSON, JSON Schemas, SVG, KML,
                    GSON, SQL, Java Spark"
                    github="https://github.com/JackPenhale/ReactTripPlanning"
                />
            </Slide>
        )
    }
    renderPiProject()
    {
        return (
            <Slide direction="left" in={this.state.showPiProject}>
                <ProjectSlide
                    picture={piImage}
                    title="Pi Playlist Selector"
                    description= "Built and developed a Rapsberry Pi heartrate sensor that takes the BPM of a user's heartrate and plays songs with a similar BPM."
                    languages="Python, GitHub, Raspbian"
                    github=""
                />
            </Slide>
        )
    }
    renderHospitalApp()
    {
        return (
            <Slide direction="left" in={this.state.showHospitalApp}>
                <ProjectSlide
                    picture={hospitalImage}
                    title="Hospital Data Application"
                    description="A web application designed to display hospital DRG data along with filters, comments and a login system."
                    languages="PHP, FuelPHP, SQL"
                    github=""
                />
            </Slide>
        )
    }
    renderResume()
    {
        return (
            <Slide direction="left" in={this.state.showResume}>
                <ProjectSlide
                    picture={resumeImage}
                    title="Digital Resume Website"
                    description="Developed a single page, responsive website to serve as my digital resume"
                    languages="Reactjs, HTML, CSS, VSCode, Github, Github Pages, Material UI, NPM, Node"
                    github="https://github.com/JackPenhale/Digital-Resume"
                />
            </Slide>
        )
    }
}