import React, { Component } from 'react';
import './App.css';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {Collapse, Typography, ThemeProvider, responsiveFontSizes, Grid, Button, Container} from "@material-ui/core";
import About from "./About.js";
import Resume from "./Resume.js";
import Projects from "./Projects.js";
import Experience from "./Experience.js"
import Nav from "./Nav.js"
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import { createMuiTheme } from '@material-ui/core/styles'


let theme = createMuiTheme({
  palette: {
    primary: {
      light: '#00d8ff',
      main: '#00d8ff',
    },
    secondary: {
      main: "#282C34",
    },
    contrastThreshold: 3,
    tonalOffset: 0.2,
  },
});
theme = responsiveFontSizes(theme);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showHome: true,
      showMain: false
    };
    this.toggleHome = this.toggleHome.bind(this);
  }
  render() {
    return (
        <div>
          <Collapse in={this.state.showMain}>
            <ThemeProvider theme={theme}>
              {this.renderNavBar()}
              <div className="main">
                <a id="about"/>
                {this.renderAbout()}
                <a id="projects"/>
                {this.renderProjects()}
                <a id="experience"/>
                {this.renderExperience()}
                <a id="resume"/>
                {this.renderResume()}
                <a id="contact"/>
                <Typography variant="h6" color="inherit" align="center">Contact me</Typography>
                {this.renderContact()}
              </div>
            </ThemeProvider>
          </Collapse>
            {this.renderHome()}
        </div>


    );
  }
  toggleHome()
  {
      const newState = this.state;
      newState.showHome = !this.state.showHome;
      newState.showMain = !this.state.showMain;
      this.setState({newState});
  }
  renderHome(){
    return (
        <Collapse in={this.state.showHome}>
        <Container  style={{backgroundColor:'#f9f9f9'}}
              maxWidth='xl'
        >
          <Grid
            xs={12}
            container
            alignItems="center"
            justify="center"
            item={true}
            style={{ minHeight: '100vh'}}
            
          >
        <Grid 
            className="homeBox" 
            container
            alignItems="center"
            justify="center"
            item={true}>
          <Grid className="home-header">
            <Typography variant="h2" style={{paddingBottom:"20px", color:'#f9f9f9'}}>JACK PENHALE</Typography>
            <Typography variant="h5" style={{color:'#f9f9f9'}}>SOFTWARE DEVELOPER <br/> PHOTOGRAPHER</Typography>

          </Grid>
          <Grid className="chevron" xs={12}>
            <ExpandMoreIcon 
              onClick={this.toggleHome}
              className="adc"
              fontSize="inherit"
              color="inherit"
              />
          </Grid>
        </Grid>
        </Grid>
        </Container>
        </Collapse>

    );
  }
  renderAbout()
  {
      return(
              <About closePage={this.toggleAbout}/>
      );
  }
  renderNavBar() {
    return (
      <Nav
        toggleHome = {this.toggleHome}
        />
    );
  }
  renderContact()
  {
    return (
      <Container 
        className="contact"
        maxWidth="xl">
            <Grid               
              container
              justify="center"
              spacing={3}
              item={true}
              > 
               <Typography variant="h6" color="inherit" align="center">
                 Please contact me if you are interested in working together! I am currently looking for opportunities.
               </Typography>
            </Grid>
            <Grid
                          container
                          justify="center"
                          spacing={3}
                          className="contactButton"
                          item={true}
                          >
              <a href="mailto:jackpenhale@gmail.com">
              <Button variant="contained" className="contactButton">Contact Me</Button>
              </a>
            </Grid>
            <hr/>
            <Grid
            container
            justify="center">
            <Typography variant="h6" color="inherit" align="center" className="footerEmail">
                 jackpenhale@gmail.com
            </Typography>
            <Grid xs={12} md={1} item={true}
            container
            justify="center">
            <a href="https://www.linkedin.com/in/jack-penhale-ba918516a/" target="_blank">
              <LinkedInIcon fontSize="large" className="footer"/>
            </a>
            <a href="https://github.com/JackPenhale" target="_blank">
              <GitHubIcon fontSize="large" className="footer"/>
            </a>
            </Grid>
            </Grid>
        </Container>
    )
  }
  renderResume()
  {
    return (
      <Resume/>
    )
  }
  renderExperience() {
    return (
      <Experience/>
    );
  }
  renderProjects() {
    return (
      <Projects/>
    )
  }
}


export default App;

